import ACTIONS from "./action";
const defaultState = {
  letter: ''
};
const drumReducer = (state = defaultState, action) => {
  switch (action.type) {
    case ACTIONS.Types.CHANGE_LETTER: {
      console.log(action);
      let newLetter = action.letter;
      return { letter: newLetter };
    }
    default:
      return state;
  }
};
export default drumReducer;