
import React, { Component } from "react";
import "./App.css";
import Display from "./pages/display";
import DrumPad from "./pages/drum-pad";
import { Provider as ReduxProvider } from "react-redux";
import configureStore from "./modules/store";
const reduxStore = configureStore(window.REDUX_INITIAL_DATA);
class App extends Component {
  render() {
    return (
      <ReduxProvider store={reduxStore}>
        <div id='drum-machine'>
          <Display />
          <DrumPad data={{letter: 'Q', padId: 'heater-1', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3'}} />
          <DrumPad data={{letter: 'W', padId: 'heater-2', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3'}} />
          <DrumPad data={{letter: 'E', padId: 'heater-3', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3'}} />
          <DrumPad data={{letter: 'A', padId: 'side-stick', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3'}} />
          <DrumPad data={{letter: 'S', padId: 'snare', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3'}} />
          <DrumPad data={{letter: 'D', padId: 'open-hi-hat', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3'}} />
          <DrumPad data={{letter: 'Z', padId: 'kick-n-hat', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3'}} />
          <DrumPad data={{letter: 'X', padId: 'kick', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3'}} />
          <DrumPad data={{letter: 'C', padId: 'punchy-kick', clipSource: 'https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3'}} />
        </div>
      </ReduxProvider>
    );
  }
}
export default App;
