import React, { Component } from "react";
import ACTIONS from "../modules/action";
import { connect } from "react-redux";

class DrumPad extends Component {
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }

  handleClick(event) {
    this.props.changeLetter(event.target.name);
    this.playClip(event.target.name);
  };

  handleKeyPress(event) {
    let letter = event.key.toUpperCase();
    if(letter === 'Q' ||
       letter === 'W' ||
       letter === 'E' ||
       letter === 'A' ||
       letter === 'S' ||
       letter === 'D' ||
       letter === 'Z' ||
       letter === 'X' ||
       letter === 'C')   {
      this.props.changeLetter(letter);
      this.playClip(letter);
    }
  }

  playClip(letter) {
    let x = document.getElementById(letter);
    x.currentTime = 0;
    x.play();
  };

  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyPress);
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyPress);
  }

  render() {
    return (
        <a href="#drum-machine" className="drum-pad" id={this.props.data.padId} onClick={this.handleClick} name={this.props.data.letter}>{this.props.data.letter}
          <audio src={this.props.data.clipSource} className="clip" id={this.props.data.letter}>
            Your browser does not support audio.
          </audio>
        </a>
    );
  }
}


const mapStateToProps = state => ({
  letter: state.letter
});
const mapDispatchToProps = dispatch => ({
  changeLetter: letter => dispatch(ACTIONS.changeLetter(letter))
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DrumPad);