import React, { Component } from "react";
// import ACTIONS from "../modules/action";
import { connect } from "react-redux";

class Display extends Component {
  constructor(props){
    super(props);
    this.displayText = this.displayText.bind(this);
  }
  
  displayText() {
    switch(this.props.letter){
    case 'Q':
      return 'heater 1';
    case 'W':
      return 'heater 2';
    case 'E':
      return 'heater 3';
    case 'A':
      return 'side stick';
    case 'S':
      return 'snare';
    case 'D':
      return 'open hi hat';
    case 'Z':
      return 'kick n hat';
    case 'X':
      return 'kick';
    case 'C':
      return 'punchy kick';
    default:
      return '';
    }
  }

  render() {
    return (
      <div id="display">
        {this.displayText()}
      </div>
    );
  }
}




const mapStateToProps = state => ({
  letter: state.letter
});
export default connect(
  mapStateToProps,
  null
)(Display);